import React, { Component } from "react";

import Project from "./Project";
import "./Project.css";

class ProjectHolder extends Component {
    renderIntroPage() {
        return (
            <div className="slide">
                <h1>Some of my project</h1>
                <p>
                    Most of it is built with MERN stack(MongoDB, Express, React,
                    Node JS)
                </p>

                <i
                    onClick={() => {
                        this.props.fullpageApi.moveSlideRight();
                    }}
                    className="fas fa-arrow-right zoom-on-hover"
                />
            </div>
        );
    }

    render() {
        return (
            <div className="section">
                {this.renderIntroPage()}
                <Project title="Social Network" description="A Website allow user to post , all posts on the website is public, but only the person who created the post can edit, remove it" />
                <Project />
                <Project />
            </div>
        );
    }
}

export default ProjectHolder;
